import React from 'react';
import './style.scss';
import * as Yup from 'yup';
import { NavLink, useNavigate } from 'react-router-dom';
import { useFormik } from 'formik';
import { uuid } from '../../utils';
import * as mdb from 'mdb-ui-kit';
import { Input, Button } from 'mdb-ui-kit';
import axios from 'axios';
import { BACKEND_DOMAIN_API } from '../../global';

const validattion = Yup.object({
  email: Yup.string('Bn cn nhp ng nh dng email!').email('Bn cn nhp ng nh dng email!').required('Bn cn cung cp email!'),
  password: Yup.string().required('Bn cn nhp mt khu!'),
  cfPassword: Yup.string().required('Bn cn nhp li mt khu!'),
  username: Yup.string().required('Bn cn nhp username!'),
  phone: Yup.string().required('Bn cn nhp s in thoi!')
})
const Register = () => {
  const navigate = useNavigate();
  const { values, touched, isValid, errors, handleSubmit, handleChange, handleBlur } = useFormik({
    initialValues: {
      id: uuid(),
      email: '',
      password: '',
      username: '',
      name: '',
      phone: '',
    },
    validationSchema: validattion,
    onSubmit(values) {
      handleRegister(values);
    }
  });

  const handleRegister = async (user) => {
    const listPrevUser = await axios.get(`${BACKEND_DOMAIN_API}/api/v1/users`);
    const arrUser = listPrevUser.data;
    const findExistUser = arrUser.findIndex((item) => item.email === user.email);
    if(findExistUser >= 0){
      alert('Email  tn ti!');
    } else{
      const requestRegiter = await axios.post(`${BACKEND_DOMAIN_API}/api/v1/users`, user);
      if(requestRegiter.status === 201){
        alert('ng k thnh cng!');
        navigate('/auth/login');
      }
    }
  } 
  return (
    <div className='container-register-page'>
      <h1 className='register-name'>ng k</h1>
      <form >
        <div className='row'>
          <label>Username <strong className='red'>*</strong></label>
        </div>
        <div className='row'>
          <input type="text" name='username' placeholder='Nhp username' value={values.username} onChange={handleChange} onBlur={handleBlur} />
        </div>
        {touched.username && !isValid && errors.username && (<p className='red'>{errors.username}</p>)}

        <div className='row'>
          <label>Name</label>
        </div>
        <div className='row'>
          <input type="text" name='name' placeholder='Nhp name'/>
        </div>

        <div className='row'>
          <label>Email <strong className='red'>*</strong></label>
        </div>
        <div className='row'>
          <input type="email" name='email' placeholder='Nhp email' value={values.email} onChange={handleChange} onBlur={handleBlur} />
        </div>
        {touched.email && !isValid && errors.email && (<p className='red'>{errors.email}</p>)}

        <div className='row'>
          <label>Phone number <strong className='red'>*</strong></label>
        </div>
        <div className='row'>
          <input type="text" name='phone' placeholder='Nhp phone number' value={values.phone} onChange={handleChange} onBlur={handleBlur} />
        </div>
        {touched.phone && !isValid && errors.phone && (<p className='red'>{errors.phone}</p>)}

        <div className='row'>
          <label>Password <strong className='red'>*</strong></label>
        </div>
        <div className='row'>
          <input type="password" name='password' placeholder='Nhp password' value={values.password} onChange={handleChange} onBlur={handleBlur} />
        </div>
        {touched.password && !isValid && errors.password && (<p className='red'>{errors.password}</p>)}

        <div className='row'>
          <label>Confirm Password <strong className='red'>*</strong></label>
        </div>
        <div className='row'>
          <input type="password" name='cfPassword' placeholder='Nhp li password' value={values.cfPassword} onChange={handleChange} onBlur={handleBlur}/>
        </div>
        {touched.cfPassword && !isValid && errors.cfPassword && (<p className='red'>{errors.cfPassword}</p>)}

        <br />

        <div className="input-form">
          <button type='submit'>ng k</button>
          <p>Bn  c ti khon? <NavLink to="./login">ng nhp</NavLink></p>
        </div>
      </form>

      {/* <button type="button" class="btn btn-success">Success</button> */}
    </div>
  )
}

export default Register